.App {
	display:flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.sudoku-board {
	display:flex;
	flex-wrap:wrap;
	width:500px; 
	height:500px;
	border: 5px solid black;
	margin-top: 50px;
}
.sudoku-grid {
	border:3px solid black; 
	width: 166.5px;
	height:167px; 
	background-color: black;
}

/* .sudoku-cell {  
  display:inline-block;
  border:1px solid black
} */

.sudoku-grid > tr > td {
  display:inline-block;
  border:1px solid black;
  width: 48px;;
  height: 47px;
  background-color: white;
}